label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.ml-30 { 
  margin-left: 30px;
}

.mt-15{
  margin-top: 15px;
}

.mt-10{
  margin-top: 6px;
  color: red;
  margin-left: 5px;
  
}

.mr-5{
  margin-right: 5px;
}

.ml-5{
  margin-left: 5px;
}

.pr-5{
  padding-right: 5px;
}

.dropdown-menu .fa:before{
  padding-right: 10px;
}

.text-right{
  float: right;
  cursor: pointer;
}

.text-right:hover{
  color: red;
}

.sort_selected::after{
  content: '\f00c';
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  margin: 0px 0px 0px 10px;
  display: inline-block;
}